body{
  background-color: #30384b;
  color: #eee; 
  font-family: system-ui;
}

main{
  padding-top: 50px;
  max-width: 340px;
  margin: 0 auto; 
  text-align: center ;
  font-family: system-ui;
    
}

button{
  background-color: transparent;
  color: rgba(255,255,255,0.8);
  border:0;
  display: inline-block;
  width: 100px;
}

button.with-text{
  background-color:rgba(255,255,255,0.1);
  border-radius: 10px;
  font-size: 1rem;
}

span{
  padding-top: 50px;
  padding-bottom: 10px;
  font-size: 2rem;
  margin: 0 auto; 
  text-align: center ;
  font-family: system-ui;
}
